
.about__container{
    display: grid;
    grid-template-columns: 35%  50%;
    gap: 15%;
}
.about{
    margin-top: 2rem;
    margin-bottom: 2rem;
    border-radius: 16px;
    box-shadow: inset -6px -6px 12px rgba(0, 0, 0, 0.8), inset 6px 6px 12px rgba(255, 255, 255, 0.4);

}

.about__me{
    margin: auto;
    width: 370px;
     height: 450px;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary-varient),
        transparent

        
    );
    display: grid;
    place-items: center;

}
.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform-origin: rotate(10deg);
}

.about__me-image img{
    width: 300px;
    border-radius: 2rem;
}

.about__me-image:hover{
    
    padding: 3rem;
    transform: rotate(10deg);
    transition: var(--transition);

}

.about__cards{
    

    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.5rem;
}

.about__card{
    background:#2c2c6c;
    border: 1px solid transparent;
    border-radius: 2rem;
    padding: 2rem;
    text-align: center;
}



.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;

}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content{
    padding: 2rem;

}
.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);

}
.para{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
}

input{
    background-color: aqua;
}




/*=============MEDIA QUARIES (MEDIUM DEVICES)=======*/
@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{
        margin: 1rem 0 1.5rem;
    }

    .para{
        display: block;
    }
    


}
/*=============MEDIA QUARIES (SMALL DEVICES)=======*/
@media screen and (max-width:600px){
    .about__me{
        width: 90%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    section >h2 {
        margin-bottom: 4rem;
    }
    .about__content{
        text-align: center;
    }
    .about__content p{
        margin: 1.5rem;

    }
    .about__me-image:hover{
    
        padding: 0px;
        transform: none;
        transition: none;
    
    }
}

